* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#root {
  display: flex;
  justify-content: center;
}
.App {
  background-color: rgb(32, 32, 32);
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
 /* height: 100vh; */
 padding-bottom: 100vh;

}

.post {
  display: flex;
  flex-direction: row;
  padding: 15px;
  border: 2px solid teal;
  margin-top: 20px;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}
